<template>
  <div>
    <el-card class="box-card">
      <stepsCmp :returnStockData="returnStockData" />
      <div class="main_group">
        <div class="handle">
          <div v-if="returnStockData.receiveStatus === 0 || returnStockData.refund === 0 || returnStockData.refundReturnStatus === 0 || returnStockData.orderStatus === 9">
            <div class="title_background"><span>退款关闭</span></div>
            <div class="claim">
              <p class="m-0">完结时间：{{ returnStockData.returnTime }}</p>
              <p class="m-0">买家已撤销本次退款申请</p>
            </div>
          </div>
          <div v-else-if="returnStockData.receiveStatus === 1">
            <div class="title_background"><span>退款成功</span></div>
            <div class="claim">
              <p class="m-0">完结时间：{{ returnStockData.returnTime }}</p>
              <p class="m-0">退款金额：￥{{ returnStockData.refundPrice }}</p>
            </div>
          </div>
          <div v-else-if="returnStockData.serviceType === 1">
            <div class="title_background"><span>请处理退货申请</span></div>
            <div class="claim">
              <p class="m-0">如果未办理请点击同意退款给买家。</p>
              <p class="m-0">如果实际已办理请与买家联系。</p>
              <p class="m-0">如果您预期未响应申请，视作同意买家申请，系统会自动退款给卖家。</p>
            </div>
            <div class="btn_group">
              <div v-if="returnStockData.refundReturnStatus === 1">
                <el-button type="primary" style="width: 150px" size="medium" round @click="onConfirmReceipt(1)">确认收货</el-button>
                <el-button style="width: 150px" size="medium" round @click="onConfirmReceipt(0)">未收到货物</el-button>
              </div>
              <div v-else>
                <el-button type="primary" style="width: 150px" size="medium" round @click="onAgreeReturnGoods(1)">同意退货退款申请</el-button>
                <router-link :to="{ path: '/refundList/refundContent/refuse', query:{ id: returnStockData.id }}"><el-button style="width: 150px" size="medium" round>拒绝退货退款申请</el-button></router-link>
              </div>
              <span v-if="returnStockData.refund === 0" class="time">还剩23时19分23秒</span>
              <el-button v-else class="time" type="text" @click="onExpress">查看物流信息</el-button>
            </div>
          </div>
          <div v-else>
            <div class="title_background"><span>请处理退款申请</span></div>
            <div class="claim">
              <p class="m-0">如果未办理请点击同意退款给买家。</p>
              <p class="m-0">如果实际已办理请与买家联系。</p>
              <p class="m-0">如果您预期未响应申请，视作同意买家申请，系统会自动退款给卖家。</p>
            </div>
            <div class="btn_group">
              <div>
                <el-button type="primary" style="width: 150px" size="medium" round @click="onConfirmReceipt(1)">同意退款</el-button>
                <el-button style="width: 150px" size="medium" round @click="onConfirmReceipt(0)">拒绝退款</el-button>
              </div>
              <span v-if="returnStockData.refund === 0" class="time">还剩23时19分23秒{{ returnStockData }}</span>
              <el-button v-else class="time" type="text" @click="onExpress">查看物流信息</el-button>
            </div>
          </div>
          <div class="log_group">
            <div class="icon"></div>
            <h4>协商历史</h4>
          </div>
          <div class="info_list">
            <div v-for="(itme, i) in log" :key="itme.id" class="info_item">
              <div class="info_box">
                <el-avatar shape="square" size="medium" src="https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png"></el-avatar>
                <div class="content_box">
                  <p>素食熊</p>
                  <p>2020-03-13  15:46:05</p>
                </div>
              </div>
              <p class="log">买家主动撤销退款申请。</p>
              <el-divider v-if="log.length-1 !== i"></el-divider>
            </div>
          </div>
        </div>
        <div class="line"></div>
        <div class="refund_info">
          <div class="refund_info_group">
            <div class="title">
              <div class="icon"></div>
              <h4 class="title_name">退款信息</h4>
            </div>
            <el-image class="goods_img" fit="cover" :src="returnStockData.prodUrl"></el-image>
            <div class="goods_info">
              <p>{{ returnStockData.prodName }}</p>
              <p>颜色分类：{{ returnStockData.itemSpec }}</p>
            </div>
            <div class="buyer_info">
              <p>买家：{{ returnStockData.buyerName }}</p>
              <p>订单编号：{{ returnStockData.orderCode }}</p>
              <p>总金额：￥{{ returnStockData.price }}</p>
            </div>
            <div class="amount_info">
              <p>退款编号：{{ returnStockData.refundCode }}</p>
              <p>退款金额：￥{{ returnStockData.number }}</p>
            </div>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import stepsCmp from '@/views/order/refund_content/steps_cmp/index.vue'

export default {
  name: 'RefundContent',

  components: {
    stepsCmp
  },

  data () {
    return {
      log: [{ id: 1 }, 2, 3, 4], // 退款日志
      returnStockData: {}, // 退款信息
    }
  },

  methods: {
    // 点击查看物流
    onExpress () {
      this.$router.push({ path: '/refundList/refundContent/RefundExpressDetail', query: { wbNo: this.returnStockData.logisticsNumber, expressCompanyName: this.returnStockData.logisticsCompany }})
    },
    // 点击同意退货
    onAgreeReturnGoods (returnType) {
      this.$confirm('是否同意退货, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.apiRefundReturnStatus(returnType)
          .then((res) => {
            if (res.data.success) {
              this.returnStockData.refundReturnStatus = returnType
              this.$message.success('同意退货成功')
            } else {
              this.$message.error(res.data.msg)
            }
          })
      })
    },
    // 点击确认收货/未收到货物
    onConfirmReceipt (returnType) {
      switch (returnType) {
        case (0): {
          this.$confirm('确定是否未收到货物, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.apiReceiveStatus(returnType)
              .then((res) => {
                if (res.data.success) {
                  this.returnStockData.receiveStatus = returnType
                  this.returnStockData.orderStatus = 9
                  this.$message.success('操作成功')
                } else {
                  this.$message.error(res.data.msg)
                }
              })
          })
          break
        }
        case (1): {
          this.$confirm('确定是否已收到货物, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.apiReceiveStatus(returnType)
              .then((res) => {
                if (res.data.success) {
                  this.returnStockData.receiveStatus = returnType
                  this.$message.success('确认收货成功')
                } else {
                  this.$message.error(res.data.msg)
                }
              })
          })
          break
        }
      }
    },

    // api-----------------------
    // 退款详情
    returnStockFindById () {
      this.$http.post(`/woho/order/returnStock/findById/${this.$route.query.id}`)
        .then((res) => {
          if (res.data.success) {
            this.returnStockData = res.data.data
            console.log('this.returnStockData', this.returnStockData)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 同意退款、未同意退款
    apiRefundStatus (returnType) {
      let data = {
        id: this.returnStockData.id,
        returnType: returnType
      }
      return this.$http.post('/woho/order/returnStock/refundStatus', data).then((res) => res)
    },
    // 确认收到、未收到
    apiReceiveStatus (returnType) {
      let data = {
        id: this.returnStockData.id,
        returnType: returnType
      }
      return this.$http.post('/woho/order/returnStock/receiveStatus', data).then((res) => res)
    },
    // 1-同意退款退货、0-未同意退款退货
    apiRefundReturnStatus (returnType) {
      let data = {
        id: this.returnStockData.id,
        returnType: returnType
      }
      return this.$http.post('/woho/order/returnStock/refundReturnStatus', data).then((res) => res)
    },
  },

  created () {
    this.returnStockFindById()
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .main_group{
      display: flex;
      justify-content: space-around;
      .handle{
        width: 49%;
        margin-top: 82px;
        .title_background{
          width: 88%;
          height: 30px;
          background-color: #f68327;
          color: #fff;
          line-height: 30px;
          padding-left: 20px;
          margin: auto;
        }
        .claim{
          width: 88%;
          margin: 15px auto;
          font-size: 15px;
          color: #666666;
        }
        .btn_group{
          margin: auto;
          width: 88%;
          display: flex;
          align-items: flex-end;
          .time{
            font-size: 10px;
            margin-left: 15px;
            color: #0591f3;
          }
        }
        .log_group{
          width: 88%;
          margin: 15px auto 0px auto;
          display: flex;
          align-items: center;
          .icon{
            width: 8px;
            height: 31px;
            background-color: #feb67c;
            margin-right: 10px;
          }
        }
        .info_list{
          width: 88%;
          margin: auto;
          .info_item{
            .info_box{
              display: flex;
              align-items: center;
              .content_box{
                font-size: 13px;
                p{
                  margin: 5px;
                }
              }
            }
            .log{
              margin: 5px;
              font-size: 13px;
            }
          }
        }
      }
      .line{
        width: 1px;
        height: 790px;
        background-color: #bfbfbf;
      }
      .refund_info{
        width: 49%;
        .refund_info_group{
          width: 88%;
          margin: 82px auto 0px auto;
          .title{
            display: flex;
            align-items: center;
            .icon{
              width: 8px;
              height: 31px;
              background-color: #feb67c;
              margin-right: 10px;
            }
            .title_name{
              margin: 0px;
            }
          }
          .goods_img{
            width: 105px;
            height: 105px;
            margin: 21px 0px;
          }
          .goods_info{
            font-size: 15px;
            color: #666666;
            margin: 40px 0px;
            line-height: 10px;
          }
          .buyer_info{
            font-size: 15px;
            margin: 40px 0px;
            line-height: 10px;
          }
          .amount_info{
            font-size: 15px;
            margin: 40px 0px;
            line-height: 10px;
          }
        }

      }
    }

  }
</style>
